<template>
  <div class="box">
    <div id="xterm"></div>
  </div>
</template>

<script>
import { Terminal } from "xterm";
import { FitAddon } from "xterm-addon-fit";
import "xterm/css/xterm.css";
import "xterm/lib/xterm.js";
export default {
  data() {
    return {
      term: "",
      socket:""
    };
  },
  mounted() {
    let url ="ws://***********";
    this.init(url);
  },
  methods: {
    initXterm() {
      this.term = new Terminal({
        rendererType: "canvas", //type of rendering
        rows: 35, // number of rows
        convertEol : true, // enabled, the cursor is set to the beginning of the next line
        scrollback : 10, // rollback amount of the terminal
        disableStdin : false, // whether to disable input
        cursorStyle : "underline", // Cursor Style
        cursorBlink : true, // cursor blinks
        theme : {
          foreground: "yellow", // font
          background : "#060101", // Background color
          cursor : "help" // Set the cursor
         }
      });
      this.term.open(document.getElementById("xterm"));
      const fitAddon = new FitAddon();
      this.term.loadAddon(fitAddon);
      // Support input And paste method
       let _this = this; //A must redefine this , or else this point there will be problems
       this.term.onData(function(key) {
        let order = ["stdin",key]; // where you enter key value is a value, the data format must be Look for the backend!!!
        _this .socket.onsend(JSON.stringify(order)); // converted to a string
       });
    },
    init(url) {
      // instantiate socket
       this.socket = new WebSocket(url);
      // listening socket connection
       this.socket.onopen = this.open;
      // Listening socket error
       this.socket.onerror = this.error;
      // listening socket message
       this.socket.onmessage = this.getMessage;
      // sending socket message
       this.socket.onsend = this.send;
    },
    open: function() {
      console.log();
      this.initXterm();}
    ,
    error:function (){ 
      console.log()}
    ,
    close:function (){ this
      .socket.close();
      console.log()}
    ,
    getMessage: function(msg) {
      this.term.write(JSON.parse(msg.data)[1]);
    },
    send: function(order) {
      this.socket.send(order);
    }
  }
};
</script>
